import { companylists,shopColumn  } from "~/api/zhijianapi";
export default {
  name: "vFooter",
  data() {
    return {
      rullist: [],
      year: '2018',
      shopColumnList: []
    };
  },

  filters: {
    updateYear(value) {
      const y =  new Date().getFullYear()
      return `2018-${y}`      
    }
  },

  methods: {
    pushUrl(path = "/") {
      path += "?timestamp=" + +new Date();
      this.$router.push(path);
    },

    list() {
      companylists({}).then(res => {
        if (res.data.sta == 1) {
          this.rullist = res.data.items;
        }
      });
    },
    getShopColumn(){
      shopColumn({
        "action":"shop",
        "code":"column",
        "is_dev":"1"
      }).then(res => {
        if (res.data.sta === 1) {
            this.shopColumnList = res.data.items;
        }
      });
    },
  },
  computed: {
    // 判断当前是否为移动端布局
    mobileStatus() {
      return this.$store.state.isMobile;
    }
  },
  created() {
    this.list();
    this.getShopColumn();
  }
};
