export default {
  name: "vHeader",
  data() {
    return {
      hideMenu: true,
      menuList:[
  {
    page: 'home',
    name: '首页',
    url: '/',
  },
  {
    page: 'product',
    name: '指间商城',
    url: '/product/list',
  },
  {
    page: 'strategy',
    name: '礼物攻略',
    url: '/strategy/list/1',
  },
  {
    page: 'aboutus',
    name: '关于我们',
    url: '/aboutus',
    items: [
      {
        page: 'culture',
        name: '公司文化',
        url: '/aboutus#culture',
      },
      {
        page: 'intro',
        name: '企业简介',
        url: '/aboutus#intro',
      },
      {
        page: 'business',
        name: '公司业务',
        url: '/aboutus#business',
      },
      {
        page: 'advantage',
        name: '我们的优势',
        url: '/aboutus#advantage',
      },
      {
        page: 'contactus',
        name: '联系我们',
        url: '/aboutus#contactus',
      },
      {
        page: 'joinus',
        name: '加入我们',
        url: '/aboutus#joinus',
      },
    ],
  },
]
    };
  },
  props: {
    isShowTop: {
      // 默认提示
      type: Boolean,
      default() {
        return false;
      }
    },
    page:{
      type: String,
      default() {
        return 'home';
      }
    },
    isBack: {
      // 默认提示
      type: Boolean,
      default() {
        return false;
      }
    },
    paramId:{
      type: String,
      default() {
        return '';
      }
    },
  },
  methods: {
    pushUrl(path = "/") {
      if (path != this.$route.path && path != "/news") {
        path += "?timestamp=" + +new Date();
        this.$router.push(path);
      } else if (path == "/news") {
        path += "?timestamp=" + +new Date();
        this.$router.replace(path);
        this.isBack = false;
      }
    },
    pushUrls(path = "/") {
        // path += "?timestamp=" + +new Date();
        // this.$router.push(path);
        this.$router.go(-1);
    },
    /**
     * 开关移动端状态下的右侧菜单
     * @param {Boolean} status 菜单显示状态
     */
    handleChangeMenu(status) {
      if (typeof status !== "boolean") {
        this.hideMenu = !this.hideMenu;
      } else {
        this.hideMenu = status;
      }
    }
  },
  computed: {
    active() {
      switch (this.$route.path) {
        case "/":
          return 0;
        case "/we":
          return 1;
        case "/news":
          return 2;
        case "/cooperation":
          return 3;
        // case "/agent":
        //   return 4;
        case "/join":
          return 4;
      }
    },
    mobileStatus() {
      return this.$store.state.isMobile;
    }
  }
};
